<template>
    <div class="skills">


        <div>
            <v-layout row wrap>
                <v-flex xs12 class="main-title">
                    <div class="svg-wrapper">
                        <svg height="60" width="320" xmlns="http://www.w3.org/2000/svg">
                            <rect class="shape" height="60" width="320" />
                        </svg>
                        <div class="text">Skills</div>
                    </div>
                </v-flex>


            </v-layout>


            <v-layout row wrap class="gallery-row">
                <div class="info-card-gallery">


                    <div class="info-card info-card-index3-skills">
                        <figure>
                            <img class="info-card__img" src="../assets/Logos/javascript.png">
                        </figure>
                        <div class="info-card__overlay">
                            <h2 class="info-card__title">Javascript</h2>
                            <div class="info-card__subcontent-skills"> Proficiency
                                <v-progress-circular v-bind:size="100" v-bind:width="15" v-bind:rotate="360" v-bind:value="javascriptValue" class="circle-progress-bar">
                                    {{ javascriptValue + '%' }}
                                </v-progress-circular>
                            </div>
                        </div>
                    </div>

                    <div class="info-card info-card-index4-skills">
                        <figure>
                            <img class="info-card__img" src="../assets/Logos/nodejs.png">
                        </figure>
                        <div class="info-card__overlay">
                            <h2 class="info-card__title">Node.js</h2>
                            <div class="info-card__subcontent-skills"> Proficiency
                                <v-progress-circular v-bind:size="100" v-bind:width="15" v-bind:rotate="360" v-bind:value="nodejsValue" class="circle-progress-bar">
                                    {{ nodejsValue + '%' }}
                                </v-progress-circular>
                            </div>
                        </div>
                    </div>

                    <div class="info-card info-card-index5-skills">
                        <figure>
                            <img class="info-card__img" src="../assets/Logos/vue.png">
                        </figure>
                        <div class="info-card__overlay">
                            <h2 class="info-card__title">Vue.js</h2>
                            <div class="info-card__subcontent-skills"> Proficiency
                                <v-progress-circular v-bind:size="100" v-bind:width="15" v-bind:rotate="360" v-bind:value="vueValue" class="circle-progress-bar">
                                    {{ vueValue + '%' }}
                                </v-progress-circular>
                            </div>
                        </div>
                    </div>

                    <div class="info-card info-card-index6-skills">
                        <figure>
                            <img class="info-card__img" src="../assets/Logos/jquery.png">
                        </figure>
                        <div class="info-card__overlay">
                            <h2 class="info-card__title">jQuery</h2>
                            <div class="info-card__subcontent-skills"> Proficiency
                                <v-progress-circular v-bind:size="100" v-bind:width="15" v-bind:rotate="360" v-bind:value="jqueryValue" class="circle-progress-bar">
                                    {{ jqueryValue + '%' }}
                                </v-progress-circular>
                            </div>
                        </div>
                    </div>


                    <div class="info-card info-card-index7-skills">
                        <figure>
                            <img class="info-card__img" src="../assets/Logos/materialize.png">
                        </figure>
                        <div class="info-card__overlay">
                            <h2 class="info-card__title">Materialize CSS</h2>
                            <div class="info-card__subcontent-skills"> Proficiency
                                <v-progress-circular v-bind:size="100" v-bind:width="15" v-bind:rotate="360" v-bind:value="materializeValue" class="circle-progress-bar">
                                    {{ materializeValue + '%' }}
                                </v-progress-circular>
                            </div>
                        </div>
                    </div>

                    <div class="info-card info-card-index8-skills">
                        <figure>
                            <img class="info-card__img" src="../assets/Logos/vuetify.png">
                        </figure>
                        <div class="info-card__overlay">
                            <h2 class="info-card__title">Vuetify.js</h2>
                            <div class="info-card__subcontent-skills"> Proficiency
                                <v-progress-circular v-bind:size="100" v-bind:width="15" v-bind:rotate="360" v-bind:value="vuetifyValue" class="circle-progress-bar">
                                    {{ vuetifyValue + '%' }}
                                </v-progress-circular>
                            </div>
                        </div>
                    </div>

                    <div class="info-card info-card-index1-skills">
                        <figure>
                            <img class="info-card__img" src="../assets/Logos/html5.png">
                        </figure>
                        <div class="info-card__overlay">
                            <h2 class="info-card__title">HTML5</h2>
                            <div class="info-card__subcontent-skills"> Proficiency
                                <v-progress-circular v-bind:size="100" v-bind:width="15" v-bind:rotate="360" v-bind:value="htmlValue" class="circle-progress-bar">
                                    {{ htmlValue + '%' }}
                                </v-progress-circular>
                            </div>
                        </div>
                    </div>

                    <div class="info-card info-card-index2-skills">
                        <figure>
                            <img class="info-card__img" src="../assets/Logos/css3.png">
                        </figure>
                        <div class="info-card__overlay">
                            <h2 class="info-card__title">CSS3</h2>
                            <div class="info-card__subcontent-skills"> Proficiency
                                <v-progress-circular v-bind:size="100" v-bind:width="15" v-bind:rotate="360" v-bind:value="cssValue" class="circle-progress-bar">
                                    {{ cssValue + '%' }}
                                </v-progress-circular>
                            </div>
                        </div>
                    </div>

                    <div class="info-card info-card-index9-skills">
                        <figure>
                            <img class="info-card__img" src="../assets/Logos/sass.png">
                        </figure>
                        <div class="info-card__overlay">
                            <h2 class="info-card__title">Sass</h2>
                            <div class="info-card__subcontent-skills"> Proficiency
                                <v-progress-circular v-bind:size="100" v-bind:width="15" v-bind:rotate="360" v-bind:value="sassValue" class="circle-progress-bar">
                                    {{ sassValue + '%' }}
                                </v-progress-circular>
                            </div>
                        </div>
                    </div>

                </div>
            </v-layout>


        </div>


    </div>
    </div>

</template>

<script>
    export default {
        name: 'skills',
        mounted() {
            window.sr = ScrollReveal({ duration: 2000 });
            sr.reveal('.main-title');
            sr.reveal('.main-subtitle');
            sr.reveal('.gallery-row');
        },
        data() {
            return {
                interval: {},
                htmlValue: 95,
                cssValue: 95,
                javascriptValue: 70,
                nodejsValue: 60,
                vueValue: 80,
                jqueryValue: 25,
                materializeValue: 97,
                vuetifyValue: 85,
                sassValue: 65,
            }
        }
    }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .main-title {
        margin-top: 3vh;
    }

    .main-subtitle {
        color: white;
        text-align: center;
        font-family: 'Cinzel';
        letter-spacing: 3px;
    }

    .info-card__subcontent-skills {
        color: #19f6e8;
    }

    .gallery-row {
        height: 25rem !important;
    }

    @media(max-width: 768px) {
        .gallery-row {
            height: 70rem !important;
        }
    }

    .gallery-row:last-of-type {
        height: auto !important;
    }

    .svg-wrapper {
        text-align: center;
        position: relative;
        top: 50%;
        transform: translateY(-50%);
        margin: 0 auto;
        width: 320px;
        height: 70px;
    }

    .shape {
        stroke-dasharray: 140 540;
        stroke-dashoffset: -474;
        stroke-width: 8px;
        fill: transparent;
        stroke: #19f6e8;
        border-bottom: 5px solid black;
        transition: stroke-width 1s, stroke-dashoffset 1s, stroke-dasharray 1s;
    }

    .text {
        font-family: 'Cinzel';
        font-size: 22px;
        line-height: 32px;
        letter-spacing: 8px;
        color: #fff;
        top: -48px;
        position: relative;
    }

    .svg-wrapper:hover .shape {
        stroke-width: 2px;
        stroke-dashoffset: 0;
        stroke-dasharray: 760;
    }
</style>


